.toggle-button {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    box-sizing: border-box;
}

.toggle-button:focus {
    outline: none;
}

.toggle_button_line {
    width: 30px;
    height: 2px;
    background: #fff;
}
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: #1da1f2;
    height: 75px;
    z-index: 999;
}

.navbar_navigation {
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 .5rem;
}

.navbar_logo img {
    margin-left: 1rem;
    height: 65px;
    width: 65px;
}

.navbar_logo a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
}

.logo_nav_item_space {
    flex: 1 1;
}

.navbar_items ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.navbar_items li {
    padding: 0 0.5rem;
}

.navbar_items a {
    color: rgb(29, 29, 29);
    text-decoration: none;
    font-size: 1rem;
}

.navbar_items a:hover,
.navbar_items a:active {
    color: white;
    text-decoration: underline;
    z-index: 1000;
}

@media (max-width: 768px) {
    .navbar_items{
        display: none;
    }
}

@media (min-width: 769px) {
    .popout_toggle {
        display: none;
    }
    .navbar_logo {
        margin-left: 0;
    }
}
.popout {
    height:100%;
    background: #fff;
    box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.3s ease-out;
    transition: -webkit-transform 0.3s ease-out;
    transition: transform 0.3s ease-out;
    transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.popout.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.popout ul {
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.popout li {
    margin: 0.5rem 0;
}

.popout a {
    color: rgb(29, 29, 29);
    text-decoration: none;
    font-size: 1.1rem;
}

.popout a:hover,
.popout a:active {
    color: #1da1f2;
}

@media (min-width: 769px) {
    .popout {
        display: none;
    }
}
.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.3);
    z-index: 100;
}
/* Home page styling */

.landing-grid {
    background: #1DA1F2;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #1DA1F2, #021B79,);  /* Chrome 10-25, Safari 5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(#1DA1F2), to(#021B79));
    background: -webkit-linear-gradient(top, #1DA1F2, #021B79);
    background: linear-gradient(to bottom, #1DA1F2, #021B79); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: center;
    position: fixed;
    top: 75px;
    left: 0;
    width: 100%;
    height: 100%;
}

.home-img {
    height: 300px;
    margin-top: 2em;
}

.banner-text {
    background-color: black;
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
}

.banner-text h1 {
    font-size: 66px;
    font-weight: bold;
    color: white;
}

.banner-text hr {
    border-top: 5px solid white;
    width: 50%;
    margin: auto;
}

.banner-text p{
    color: white;
